import { useReadTextFromClipboard } from '@vkruglikov/react-telegram-web-app';

import { Button, Form, Input, InputNumber, InputRef, Select, Space, Typography } from 'antd';
import { SettingOutlined } from '@ant-design/icons';
import { CopyOutlined } from '@ant-design/icons';
import { Clipboard, Copy } from 'react-bootstrap-icons';
import { useRef, useState } from 'react';
import NumericInput from './NumericInput';

import { useProxyRef } from 'react-proxy-ref';

let d = document;

const Calculator = () => {
  const readText = useReadTextFromClipboard();

  const [sum, setSum] = useState('');
  const [courtType, setCourtType] = useState('obsh');
  const [poshVal, setPoshVal] = useState('');
  const inputSumEl = useRef<HTMLInputElement>(null);

  const WebApp = useWebApp();

  console.log('WebApp.version: ', WebApp.version);
  console.log('WebApp: ', WebApp)
  
  const refSum = useRef(null);

  const copyTextToClipboard = async (text: string) => {
    try {
      await navigator.clipboard.writeText(text);
      console.log('Значение успешно скопировано в буфер обмена!');
    } catch (err) {
      console.error('Ошибка:', err);
    }
  };

  const copyTextFromClipboard = (e:any) => {
    let c = WebApp.readTextFromClipboard().then((value:any)=>console.log(value));
    console.log(c);
    
    /*readText().then((value)=>{
        console.log('Clipboard text: ', value);
      }
    );*/

    /*try {
      await navigator.clipboard.readText().then((text) => {
        let inputsum = d.querySelector('input#sum.ant-input-number-input') as HTMLInputElement;
        setSum(text);
        inputsum.value = text;
        console.log('---- text:', text);
        let gospl = calcPosh(text).gosp;
        console.log(gospl);
        setPoshVal(gospl.toString().replace('.',','));

      });
      
    } catch (err) {
      console.error('Ошибка:', err);
    }*/

  }


  const onKeyPress = (e: React.KeyboardEvent) => {
    const specialCharRegex = new RegExp("[a-zA-Z0-9@.' ,-]");
    const pressedKey = String.fromCharCode(!e.charCode ? e.which : e.charCode);
    if (!specialCharRegex.test(pressedKey)) {
      e.preventDefault();
      return false;
    }
  }

  function human(dosum: any) { // разрядность
    let sum = ""+dosum;
    sum = sum.replace(/(\d)(?=(\d\d\d)+([^\d]|$))/g, '$1 ') // разрядность
    if (sum.indexOf(".")!==-1) {						   // к десяткам копеек добавляем ноль
      var b=sum.substr(sum.indexOf("."),sum.length)
      if (b.length===2) sum+="0"
    }
    if (sum.indexOf(",")!==-1) {						   // к десяткам копеек добавляем ноль
      var b=sum.substr(sum.indexOf(","),sum.length)
      if (b.length===2) sum+="0";
    }
    sum = sum.replace(/^00\./,'0.'); // меняем 00. на 0,
    sum = sum.replace(/^00,/,'0.');
    sum = sum.replace(/\./g,','); // меняем тчк на зпт
    return sum;
  }
  
  function calcPosh(value: any) {
    //console.log('--- value: ',Number(value.replace(',','.')));
    //console.log('--- type: ',typeof(value));
    let sum;
    if (typeof(value) === 'string') {
      sum = Number(value.replace(',','.'));
    } else {
      sum = Number(value);
    }
    let gosp1=0;
    let gosp2=0;
    let opis=0;
    console.log('--- sum: ',sum);
    console.log('--- courtType: ',courtType);
    if (courtType==='obsh') {
      if (sum<=100000) {gosp1=4000; opis=1}
      if (sum>100000 && sum<=300000) {gosp1=(sum-100000)/100*3+4000; opis=2}
      if (sum>300000 && sum<=500000) {gosp1=(sum-300000)/100*2.5+10000; opis=3}
      if (sum>500000 && sum<=1000000) {gosp1=(sum-500000)/100*2+15000; opis=4}
      if (sum>1000000 && sum<=3000000) {gosp1=(sum-1000000)/100+25000; opis=5}
      if (sum>3000000 && sum<=8000000) {gosp1=(sum-3000000)/100*0.7+45000; opis=6}
      if (sum>8000000 && sum<=24000000) {gosp1=(sum-8000000)/100*0.35+80000; opis=7}
      if (sum>24000000 && sum<=50000000) {gosp1=(sum-24000000)/100*0.3+136000; opis=8}
      if (sum>50000000 && sum<=100000000) {gosp1=(sum-50000000)/100*0.2+214000; opis=9}
      if (sum>100000000) {gosp1=(sum-100000000)/100*0.15+314000; opis=10}
      if (gosp1>900000) gosp1=900000;
      return {gosp:gosp1.toFixed(2), opis:opis}
    } else {
      if (sum<=100000) {gosp2=10000;opis=1}
      if (sum>100000 && sum<=1000000) {gosp2=(sum-100000)/100*5+10000; opis=2}
      if (sum>1000000 && sum<=10000000) {gosp2=(sum-1000000)/100*3+55000; opis=3}
      if (sum>10000000 && sum<=50000000) {gosp2=(sum-10000000)/100+325000; opis=4}
      if (sum>50000000) {gosp2=(sum-50000000)/100*0.5+725000; opis=5}
      if (gosp2>10000000) gosp2=10000000
      return {gosp:gosp2, opis:0}
    }
  }
  
  return (
    <>
      <Typography.Title level={3}>Калькулятор госпошлины</Typography.Title>
      <Form
        labelCol={{ span: 6 }}
        name="Calculator"
        layout="horizontal"
        autoComplete="off"
        initialValues={{
          "courtType": "obsh" 
        }}
      >
        <Form.Item label="Вид суда" name={'courtType'}>
          <Select
            value={courtType}
            onChange={value => setCourtType(value)}
          >
            <Select.Option defaultValue={'obsh'} value="obsh">Суд общей юрисдикции, мировой судья</Select.Option>
            <Select.Option value="arb">Арбитражный суд</Select.Option>
          </Select>
        </Form.Item>
        <div>{sum}</div>
        <Form.Item label='Цена иска'>
          <Space.Compact id='space_sum' style={{width: '100%'}}>
            <InputNumber 
              id='sum'
              ref={inputSumEl}
              size='large'
              value={sum}
              style={{ 
                width: '89%' 
              }}
              decimalSeparator={','}
              formatter={value=>human(value)}
              suffix=' '
              onChange={value => {
                if (value===null) setSum('');
                if (value) {
                  setSum(value.toString());
                }
                let gospl = calcPosh(value).gosp;
                setPoshVal(gospl.toString().replace('.',','));
              }}
              onKeyUp={onKeyPress}
              min={'0'}
              step={0.01}
              precision={2}
              placeholder='Цена иска'
            />
            <Button type="primary" size='large' onClick={(e) => copyTextFromClipboard(e)}><Clipboard/></Button>
          </Space.Compact>
        </Form.Item>
        {/*<NumericInput style={{ textAlign: 'right' }} value={value} onChange={setValue}/>*/}
        <Form.Item label='Размер пошлины'>
          <div style={{display: 'none'}}>{poshVal}</div>
          <Space.Compact id='space_posh' style={{width: '100%'}}>
            <InputNumber
              id='posh'
              size='large'
              style={{
                width: '89%'
              }}
              value={poshVal}
              onChange={value => poshVal}
              onKeyDown={e => {
                  console.log(e);
                  e.preventDefault();
                }
              }
              decimalSeparator={','}
              formatter={value=>human(value)}
              suffix=' '
              readOnly={true}  
            />
            <Button type="primary" size='large' onClick={() => {
                //copyTextToClipboard(poshVal);
                console.log(WebApp);
                
                //WebApp.openTelegramLink('https://t.me/trendingapps');
              }}
            ><Copy/></Button>
          </Space.Compact>
        </Form.Item>
        
      </Form>
    </>
  );
};
export default Calculator;